/* --------------------------------

Global style

-------------------------------- */
div {
  font-family: "华文细黑", "Microsoft YaHei", "微软雅黑", Helvetica, Tahoma, Arial, STXihei, sans-serif;
}
a,
a:hover,
a:active,
a:before,
a:after,
a:visited {
  text-decoration: none;
  color: #000;
}
li {
  list-style-type: none;
}
body,
header {
  margin: 0;
  padding: 0;
  background-color: #f8f9fb;
}
.hidden,
.show {
  transition: 0.5s;
}
.hidden {
  opacity: 0;
  visibility: hidden;
}
.show {
  opacity: 1;
  visibility: visible;
}


